'use strict';

//  exit   

import Http from "../modules/http";

/**
 *     
 * @module UserService
 */
export default class UserService {
	constructor() {
		this.user = null;
		this.users = [];
	}

	/**
	 *   
	 * @param {string} username
	 * @param {string} email
	 * @param {string} password
	 * @param {string} confirm
	 */
	signup(username, email, password, confirm) {//  JSON
		return new Promise(function (resolve, reject) {
			if (username.length < 4) {
				throw new Error("      4 !");
			}
			if (username.length > 12) {
				throw new Error("     12 !");
			}
			if (password.length < 6) {
				throw new Error("      6 !");
			}
			if (password !== confirm) {
				throw new Error("  !!!");
			}
			if (password === username) {
				throw new Error("     !");
			}

			resolve(Http.FetchPost('/user/sign_up', {username, email, password})
				.then(function(resp) {
					console.log("username: " + resp.username);
					this.user = resp;
					return resp;
				}.bind(this))
				.then(function (resp) {
					return resp;
				}.bind(this))
				.catch(function(err) {//   errorMessage
					console.info(err);
					console.log("err response status "  + err.errorMessage);
					throw new Error(err.errorMessage);
				}.bind(this)));
		}.bind(this))
	}


	/**
	 *  
	 * @param {string} login
	 * @param {string} password
	 */
	login(login, password) {
		return new Promise(function (resolve, reject) {
			if (login.length < 4) {
				throw new Error("      4 !", null);
			}
			if (login.length > 12) {
				throw new Error("     12 !", null);
			}
			if (password.length < 6) {
				throw new Error("      6 !", null);
			}
			if (password === login) {
				throw new Error("     !", null);
			}
			resolve(Http.FetchPost('/user/sign_in', {login, password})
				.then(function(resp) {
					console.log("username: " + resp.username);
					this.user = resp;
					return resp;
				}.bind(this))
				.catch(function(err) {//   errorMessage
					console.log(err.errormessage);
					console.log("err response status "  + err.errorMessage);
					throw new Error(err.errorMessage);
				}.bind(this)));
		}.bind(this))
	}


	/**
	 *    
	 * @param {string} username
	 * @param {string} email
	 * @param {string} password
	 * @param {string} old_password
	 */
	update(username, email, password, old_password) {//  JSON
		return new Promise(function (resolve, reject) {
			if (username.length < 4) {
				throw new Error("      4 !");
			}
			if (username.length > 12) {
				throw new Error("     12 !");
			}
			if (password.length < 6) {
				throw new Error("      6 !");
			}
			if (old_password.length < 6) {
				throw new Error("      6 !");
			}
			if (password === username) {
				throw new Error("     !");
			}
			if (old_password === username) {
				throw new Error("     !");
			}

			resolve(Http.FetchPost('/user/update', {username, email, password, old_password})
				.then(function(resp) {
					this.user = resp;
					console.log("username: " + resp.username);
					return resp;
				}.bind(this))
				.catch(function(err) {//   errorMessage
					console.info(err);
					console.log("err response status "  + err.errorMessage);
					throw new Error(err.errorMessage);
				}.bind(this)));
		}.bind(this))
	}



	/**
	 * ,   
	 * @return {boolean}
	 */
	isLoggedIn() {
		return !!this.user;
	}


	/**
	 * ,   
	 * @param force -        true
	 * @return {Promise} -        
	 */
	getDataFetch(force = false) {
		if (this.isLoggedIn() && !force) {
			return new Promise(function(resolve, reject) {
				resolve(this.user);
			}.bind(this));
		}
		return Http.FetchGet('/user/whoisit')
			.then(function(resp) {
				this.user = resp;
				return this.user;
			}.bind(this))
			.catch(function (err) {
				this.user = null;
				console.log(err.statusText);
				throw new Error("Can not get response =(")
			}.bind(this))
	}


	/**
	 * 
	 */
	async logout() {
		if (this.isLoggedIn()) {
			this.user = null;
			this.users = [];
			Http.FetchGet('/user/exit')
				.then(() => {
					this.getDataFetch();
				})
				.catch(function (err) {//   
					console.log(err.errorMessage);//   ,     whoiit  
				});
		}
	}


	loadUsersList() {
		return Http.FetchGet('')
	}

	/**
	 *    
	 * @param callback
	 */
	// loadUsersList(callback) {
	// 	Http.Get('/users', function (err, users) {
	// 		if (err) {
	// 			return callback(err, users);
	// 		}
	//
	// 		this.users = users;
	//
	// 		if (this.isLoggedIn()) {
	// 			this.users = this.users.map(user => {
	// 				if (user.email === this.user.email) {
	// 					user.me = user;
	// 				}
	// 				return user;
	// 			});
	// 		}
	//
	// 		callback(null, this.users);
	// 	}.bind(this));
	// }
}



