
'use strict';
// Fetch post       json c 
	const backendUrl = "https://backend-java-spring.herokuapp.com";
	const baseUrl = `${window.location.protocol}//${window.location.host}`;

	console.log("baseUrl = ", baseUrl);
	/**
	 * ,     HTTP-
	 * @module Http
	 */

	export default class Http {
		//   throw response
		/**
		 *  GET-   
		 * @param {string} address -  
		 */
		static FetchGet(address) {
			const url = backendUrl + address;
			const myHeaders = new Headers();
			myHeaders.append("Content-Type", 'application/json; charset=utf-8');
			return fetch(url, {
				method: 'GET',
				mode: 'cors',
				credentials: 'include',
				headers: myHeaders,
			})
				.then(function (response) {
				let json = response.json();
				if (response.status >= 400) {
					return json.then(resp => {throw resp});
				}
				return json.then(resp => {return resp});
			});
		}

		/**
		 *  POST-   
		 * @param {Object} body - body-request
		 * @param {string} address -  
		 */

		//    json errMessage.
		static async FetchPost(address, body) {
			const url = backendUrl + address;
			const myHeaders = new Headers();
			myHeaders.set("Content-Type", "application/json; charset=utf-8");
			return await fetch(url, {
				method: 'POST',
				mode: 'cors',
				credentials: 'include',
				body: JSON.stringify(body),
				headers: myHeaders
			}).then(function (response) {
				let json = response.json();
				if (response.status >= 400) {
					return json.then(resp => {throw resp});
				}
				return json.then(resp => {return resp});
			});
		}
	}
	Http.BaseUrl = null;



