'use strict';

import Block from "../block/block";

/**
 *          html 
 * @module Message
 */
export default class Message extends Block {
	/**
	 *   div CSS  message
	 * @constructor
	 */
	constructor() {
		const el = document.createElement('div');
		super(el);

		this.el.classList.add("message");

		// el.classList.add('message');

	}


	/**
	 *   HTMLElement
	 */
	reset() {
		this.el.reset();
	}
}



